/* App.css */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

body {
  font-family: 'Inter', sans-serif;
  color: #333;
  line-height: 1.6;
}

.App {
  position: relative;
}

.nav-link {
  padding: 0.5rem 0.75rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.section-title {
  font-size: 2rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  position: relative;
}

.section-title::after {
  content: '';
  height: 1px;
  background-color: #e2e8f0;
  flex-grow: 1;
  margin-left: 1rem;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fadeIn {
  animation: fadeIn 0.5s ease-out forwards;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .section-title {
    font-size: 1.5rem;
  }
}
